export default class Download {
    constructor(triggers) {
        this.triggers = document.querySelectorAll(triggers);
        this.path = 'assets/document.pdf';
    }

    download(path) {
        // ÑÐ¾Ð·Ð´Ð°ÐµÐ¼ ÑÑÑÐ»ÐºÑ, Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ð°ÑÑÐ¸Ð±ÑÑÑ href Ð¸ download
        const link = document.createElement('a');
        link.setAttribute('href', path);
        link.setAttribute('download', 'NiceName');
        link.style.display = 'none';
        document.body.append(link);
        // Ð²ÑÐ·ÑÐ²Ð°ÐµÐ¼ ÑÐ¾Ð±ÑÑÐ¸Ðµ ÐºÐ»Ð¸ÐºÐ°, ÑÑÐ¾Ð±Ñ Ð½Ð°ÑÐ°ÑÑ ÑÐºÐ°ÑÐ¸Ð²Ð°Ð½Ð¸Ðµ pdf
        link.click();
        // Ð±Ð¾Ð»ÑÑÐµ ÑÑÑÐ»ÐºÐ° Ð½Ðµ Ð½ÑÐ¶Ð½Ð°, Ð¿Ð¾ÑÑÐ¾Ð¼Ñ ÑÐ´Ð°Ð»ÑÐµÐ¼ ÐµÐµ Ð¸Ð· DOM
        link.remove();
    }

    init() {
        this.triggers.forEach(item => {
            item.addEventListener('click', () => {
                this.download(this.path);
            });
        });
    }
}