export default class Slider {

    constructor({container, next, prev} = {}) {
        this.error = false;

        this.container = document.querySelector(container); // ÐºÐ¾Ð½ÑÐµÐ¹Ð½ÐµÑ ÑÐ»Ð°Ð¹Ð´Ð¾Ð²
        if (this.container === null) {
            this.error = true;
            return;
        }
        this.slides = this.container.children; // Ð²ÑÐµ ÑÐ»Ð°Ð¹Ð´Ñ ÑÐ»Ð°Ð¹Ð´ÐµÑÐ°
        if (this.slides.length === 0) {
            this.error = true;
            return;
        }

        this.next = document.querySelectorAll(next); // ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð²Ð¿ÐµÑÐµÐ´
        this.prev = document.querySelectorAll(prev); // ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð½Ð°Ð·Ð°Ð´
        this.slideIndex = 0; // Ð¸Ð½Ð´ÐµÐºÑ ÑÐµÐºÑÑÐµÐ³Ð¾ ÑÐ»Ð°Ð¹Ð´Ð°, Ð½Ð°ÑÐ¸Ð½Ð°Ñ Ñ Ð½ÑÐ»Ñ
    }

    // Ð¿Ð¾ÐºÐ°Ð·ÑÐ²Ð°ÐµÑ ÑÐ»Ð°Ð¹Ð´ Ñ Ð¸Ð½Ð´ÐµÐºÑÐ¾Ð¼ slideIndex
    showSlide() {
        // ÐµÑÐ»Ð¸ Ð¸Ð½Ð´ÐµÐºÑ ÑÐ»Ð°Ð¹Ð´Ð° Ð²ÑÑÐ¾Ð´Ð¸Ñ Ð·Ð° Ð¿ÑÐµÐ´ÐµÐ»Ñ
        if (this.slideIndex >= this.slides.length) {
            this.slideIndex = 0;
        }
        if (this.slideIndex < 0) {
            this.slideIndex = this.slides.length - 1;
        }
        // ÑÐ½Ð°ÑÐ°Ð»Ð° ÑÐºÑÑÐ²Ð°ÐµÐ¼ Ð²ÑÐµ ÑÐ»Ð°Ð¹Ð´Ñ...
        for (let i = 0; i < this.slides.length; i++) {
            this.slides[i].style.display = 'none';
        }
        // ...Ð¿Ð¾ÑÐ¾Ð¼ Ð¿Ð¾ÐºÐ°Ð·ÑÐ²Ð°ÐµÐ¼ Ð½ÑÐ¶Ð½ÑÐ¹
        this.slides[this.slideIndex].style.display = 'block';
    }

    // ÑÐ¼ÐµÑÐ°ÐµÑ ÑÐºÐ°Ð·Ð°ÑÐµÐ»Ñ ÑÐµÐºÑÑÐµÐ³Ð¾ ÑÐ»Ð°Ð¹Ð´Ð° Ð½Ð° ÑÐ¸ÑÐ»Ð¾ number; ÑÐ¸ÑÐ»Ð¾ Ð¼Ð¾Ð¶ÐµÑ
    // Ð±ÑÑÑ ÐºÐ°Ðº Ð¿Ð¾Ð»Ð¾Ð¶Ð¸ÑÐµÐ»ÑÐ½ÑÐ¼ (Ð²Ð¿ÐµÑÐµÐ´), ÑÐ°Ðº Ð¸ Ð¾ÑÑÐ¸ÑÐ°ÑÐµÐ»ÑÐ½ÑÐ¼ (Ð½Ð°Ð·Ð°Ð´)
    shiftSlider(number = 1) {
        this.slideIndex = this.slideIndex + number;
        this.showSlide();
    }

    // Ð²ÑÐ¿Ð¾Ð»Ð½ÑÐµÑ Ð²ÑÑ ÑÐ°Ð±Ð¾ÑÑ Ð¿Ð¾ Ð¿Ð¾Ð´Ð³Ð¾ÑÐ¾Ð²ÐºÐµ ÑÐ»Ð°Ð¹Ð´ÐµÑÐ° Ðº ÑÐ°Ð±Ð¾ÑÐµ
    // Ð¸ Ð¿Ð¾ÐºÐ°Ð·ÑÐ²Ð°ÐµÑ Ð¿ÐµÑÐ²ÑÐ¹ ÑÐ»Ð°Ð¹Ð´
    render() {
        if (this.error) return;

        this.next.forEach(item => {
            // Ð¿ÑÐ¸ ÐºÐ»Ð¸ÐºÐµ Â«Ð²Ð¿ÐµÑÐµÐ´Â» â Ð¿ÐµÑÐµÑÐ¾Ð´ Ðº ÑÐ»ÐµÐ´ÑÑÑÐµÐ¼Ñ ÑÐ»Ð°Ð¹Ð´Ñ
            item.addEventListener('click', () => {
                this.shiftSlider();
            });
        });

        this.prev.forEach(item => {
            // Ð¿ÑÐ¸ ÐºÐ»Ð¸ÐºÐµ Â«Ð½Ð°Ð·Ð°Ð´Â» â Ð¿ÐµÑÐµÑÐ¾Ð´ Ðº Ð¿ÑÐµÐ´ÑÐ´ÑÑÐµÐ¼Ñ ÑÐ»Ð°Ð¹Ð´Ñ
            item.addEventListener('click', () => {
                this.shiftSlider(-1);
            });
        });

        // Ð¿Ð¾ÐºÐ°Ð·Ð°ÑÑ Ð¿ÐµÑÐ²ÑÐ¹ ÑÐ»Ð°Ð¹Ð´ (Ñ Ð¸Ð½Ð´ÐµÐºÑÐ¾Ð¼ Ð½Ð¾Ð»Ñ)
        this.showSlide();
    }
}