export default class VideoPlayer {

    constructor(triggers, overlay) {
        this.triggers = document.querySelectorAll(triggers);
        this.overlay = document.querySelector(overlay);
        this.close = this.overlay.querySelector('.close');
        this.player = undefined;
        this.options = undefined; // Ð¾Ð¿ÑÐ¸Ð¸ Ð¿Ð»ÐµÐµÑÐ°
    }

    // Ð¾Ð±ÑÐ°Ð±Ð¾ÑÐºÐ° ÑÐ¾Ð±ÑÑÐ¸Ñ ÐºÐ»Ð¸ÐºÐ° Ð´Ð»Ñ Ð²ÑÐµÑ ÐºÐ½Ð¾Ð¿Ð¾Ðº Ð·Ð°Ð¿ÑÑÐºÐ° Ð²Ð¸Ð´ÐµÐ¾
    bindTriggers() {
        this.triggers.forEach(item => {
            item.addEventListener('click', () => {
                this.handlePlayClick(item);
            });
        });
    }

    // Ð¾Ð±ÑÐ°Ð±Ð¾ÑÐºÐ° ÑÐ¾Ð±ÑÑÐ¸Ñ ÐºÐ»Ð¸ÐºÐ° Ð´Ð»Ñ Ð¾Ð´Ð½Ð¾Ð¹ ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð·Ð°Ð¿ÑÑÐºÐ° Ð²Ð¸Ð´ÐµÐ¾
    handlePlayClick(item) {
        this.overlay.style.display = 'flex';
        // ÐµÑÐ»Ð¸ Ð¿Ð»ÐµÐµÑ ÑÐ¶Ðµ Ð±ÑÐ» ÑÐ¾Ð·Ð´Ð°Ð½ ÑÐ°Ð½ÐµÐµ Ñ Ð´ÑÑÐ³Ð¸Ð¼ Ð²Ð¸Ð´ÐµÐ¾, Ð½ÑÐ¶Ð½Ð¾ ÑÐ´Ð°Ð»Ð¸ÑÑ
        // iframe#frame Ð¸ Ð²Ð¾ÑÑÑÐ°Ð½Ð¾Ð²Ð¸ÑÑ div#frame, ÑÑÐ¾Ð±Ñ ÑÐ¾Ð·Ð´Ð°ÑÑ Ð½Ð¾Ð²ÑÐ¹
        if (this.player) {
            this.player.destroy();
        }
        this.createPlayer(item.dataset.url);
    }

    // ÑÑÑÐ°Ð½Ð°Ð²Ð»Ð¸Ð²Ð°ÐµÐ¼ Ð½Ð°ÑÑÑÐ¾Ð¹ÐºÐ¸ Ð¿Ð»ÐµÐµÑÐ° Ð¿ÐµÑÐµÐ´ ÐµÐ³Ð¾ ÑÐ¾Ð·Ð´Ð°Ð½Ð¸ÐµÐ¼
    setPlayerOptions() {
        this.options = {
            height: '100%',
            width: '100%'
        }
    }

    // ÑÐ¾Ð·Ð´Ð°ÐµÐ¼ Ð¿ÑÐ¾Ð¸Ð³ÑÑÐ²Ð°ÑÐµÐ»Ñ Ñ Ð¸ÑÐ¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°Ð½Ð¸ÐµÐ¼ YouTube Player API
    createPlayer(id) {
        this.setPlayerOptions();
        this.options.videoId = id;
        this.player = new YT.Player('frame', this.options);
    }

    // Ð¿ÑÐ¸ ÐºÐ»Ð¸ÐºÐµ Ð½Ð° ÐºÑÐµÑÑÐ¸Ðº ÑÐ±Ð¸ÑÐ°ÐµÐ¼ overlay Ð¸ Ð¾ÑÑÐ°Ð½Ð°Ð²Ð»Ð¸Ð²Ð°ÐµÐ¼ Ð²Ð¸Ð´ÐµÐ¾
    bindClose() {
        this.close.addEventListener('click', () => {
            this.overlay.style.display = 'none';
            this.player.stopVideo();
        });
    }

    // Ð¸Ð½Ð¸ÑÐ¸Ð°Ð»Ð¸Ð·Ð°ÑÐ¸Ñ Ð¿ÑÐ¾Ð¸Ð³ÑÑÐ²Ð°ÑÐµÐ»Ñ, Ð¿Ð¾Ð´ÐºÐ»ÑÑÐµÐ½Ð¸Ðµ API Ð¸ Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸ÐºÐ¸ ÑÐ¾Ð±ÑÑÐ¸Ð¹
    init() {
        // Ð¿Ð¾Ð´ÐºÐ»ÑÑÐ°ÐµÐ¼ YouTube Player API
        const tag = document.createElement('script');
        tag.src = 'https://www.youtube.com/iframe_api';
        const firstScriptTag = document.getElementsByTagName('script')[0];
        firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
        // Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸ÐºÐ¸ ÑÐ¾Ð±ÑÑÐ¸Ñ ÐºÐ»Ð¸ÐºÐ° Ð´Ð»Ñ Ð²ÑÐµÑ ÐºÐ½Ð¾Ð¿Ð¾Ðº Ð·Ð°Ð¿ÑÑÐºÐ° Ð²Ð¸Ð´ÐµÐ¾
        this.bindTriggers();
        // Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸Ðº ÑÐ¾Ð±ÑÑÐ¸Ñ ÐºÐ»Ð¸ÐºÐ° Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑ ÐºÑÐµÑÑÐ¸ÐºÐ° (Ð·Ð°ÐºÑÑÑÑ)
        this.bindClose();
    }
}