import Slider from './Slider';

export default class MiniSlider extends Slider {

    constructor({container, next, prev, autoplay, current}) {
        super({container: container, next: next, prev: prev});
        this.autoplay = autoplay ? true : false;
        // ÐµÑÐ»Ð¸ Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»Ñ ÑÐ¾ÑÐµÑ ÑÐ°Ð¼ ÑÐ¿ÑÐ°Ð²Ð»ÑÑÑ Ð¿Ð¾ÐºÑÑÑÐºÐ¾Ð¹ ÑÐ»Ð°Ð¹Ð´ÐµÑÐ°, ÑÐ¾
        // autoplay Ð±ÑÐ´ÐµÑ ÐµÐ¼Ñ Ð¼ÐµÑÐ°ÑÑ; Ð¿Ð¾ÑÑÐ¾Ð¼Ñ Ð°Ð²ÑÐ¾Ð¿ÑÐ¾ÐºÑÑÑÐºÐ° Ð½Ðµ Ð±ÑÐ´ÐµÑ
        // ÑÐ°Ð±Ð¾ÑÐ°ÑÑ, ÐºÐ¾Ð³Ð´Ð° ÑÐºÐ°Ð·Ð°ÑÐµÐ»Ñ Ð¼ÑÑÐ¸ Ð½Ð°Ð´ Ð±Ð»Ð¾ÐºÐ¾Ð¼ wrapper, ÐºÐ¾ÑÐ¾ÑÑÐ¹
        // ÑÐ¾Ð´ÐµÑÐ¶Ð¸Ñ ÑÐ°Ð¼ ÑÐ»Ð°Ð¹Ð´ÐµÑ Ð¸ ÐºÐ½Ð¾Ð¿ÐºÐ¸ ÑÐ¿ÑÐ°Ð²Ð»ÐµÐ½Ð¸Ñ
        this.playing = undefined;
        try {
            this.wrapper = this.container.parentNode;
        } catch (e) {
            this.error = true;
        }
        // Ð´Ð¾Ð¿Ð¾Ð»Ð½Ð¸ÑÐµÐ»ÑÐ½ÑÐ¹ css-ÐºÐ»Ð°ÑÑ Ð´Ð»Ñ Ð°ÐºÑÐ¸Ð²Ð½Ð¾Ð³Ð¾ ÑÐ»Ð°Ð¹Ð´Ð°
        this.current = current;
    }

    // Ð²ÑÐ¿Ð¾Ð»Ð½ÑÐµÑ Ð²ÑÑ ÑÐ°Ð±Ð¾ÑÑ Ð¿Ð¾ Ð¿Ð¾Ð´Ð³Ð¾ÑÐ¾Ð²ÐºÐµ ÑÐ»Ð°Ð¹Ð´ÐµÑÐ° Ðº ÑÐ°Ð±Ð¾ÑÐµ
    render() {
        if (this.error) return;

        this.next.forEach(item => {
            item.addEventListener('click', () => {
                this.forward();
            });
        });

        this.prev.forEach(item => {
            item.addEventListener('click', () => {
                this.backward();
            });
        });

        // Ð·Ð°Ð¿ÑÑÐºÐ°ÐµÐ¼ Ð°Ð²ÑÐ¾Ð¼Ð°ÑÐ¸ÑÐµÑÐºÑÑ Ð¿ÑÐ¾ÐºÑÑÑÐºÑ
        if (this.autoplay) {
            this.startPlay();
            // ÐºÐ¾Ð³Ð´Ð° Ð¼ÑÑÑ Ð½Ð°Ð´ Ð±Ð»Ð¾ÐºÐ¾Ð¼ wrapper â Ð¾ÑÑÐ°Ð½Ð°Ð²Ð»Ð¸Ð²Ð°ÐµÐ¼ Ð°Ð²ÑÐ¾Ð¼Ð°ÑÐ¸ÑÐµÑÐºÑÑ Ð¿ÑÐ¾ÐºÑÑÑÐºÑ
            this.wrapper.addEventListener('mouseenter', () => {
                this.stopPlay();
            });
            // ÐºÐ¾Ð³Ð´Ð° Ð¼ÑÑÑ Ð¿Ð¾ÐºÐ¸Ð´Ð°ÐµÑ Ð±Ð»Ð¾Ðº wrapper â Ð·Ð°Ð¿ÑÑÐºÐ°ÐµÐ¼ Ð°Ð²ÑÐ¾Ð¼Ð°ÑÐ¸ÑÐµÑÐºÑÑ Ð¿ÑÐ¾ÐºÑÑÑÐºÑ
            this.wrapper.addEventListener('mouseleave', () => {
                this.startPlay();
            });
        }
    }

    // Ð·Ð°Ð¿ÑÑÐºÐ°ÐµÑ Ð°Ð²ÑÐ¾Ð¼Ð°ÑÐ¸ÑÐµÑÐºÑÑ Ð¿ÑÐ¾ÐºÑÑÑÐºÑ
    startPlay() {
        this.playing = setInterval(() => {
            this.forward();
        }, 3000);
    }

    // Ð¾ÑÑÐ°Ð½Ð°Ð²Ð»Ð¸Ð²Ð°ÐµÑ Ð°Ð²ÑÐ¾Ð¼Ð°ÑÐ¸ÑÐµÑÐºÑÑ Ð¿ÑÐ¾ÐºÑÑÑÐºÑ
    stopPlay() {
        clearInterval(this.playing);
    }

    // Ð²Ð¿ÐµÑÐµÐ´ â Ð¿ÐµÑÐ²Ð°Ñ ÐºÐ°ÑÑÐ¸Ð½ÐºÐ° Ð¿Ð¾Ð¼ÐµÑÐ°ÐµÑÑÑ Ð¿Ð¾ÑÐ»Ðµ Ð¿Ð¾ÑÐ»ÐµÐ´Ð½ÐµÐ¹
    forward() {
        while (this.slides[1].tagName === 'BUTTON') {
            this.container.append(this.slides[1]);
        }
        this.container.append(this.slides[0]);
        this.active();
    }

    // Ð½Ð°Ð·Ð°Ð´ â Ð¿Ð¾ÑÐ»ÐµÐ´Ð½ÑÑ ÐºÐ°ÑÑÐ¸Ð½ÐºÐ° Ð¿Ð¾Ð¼ÐµÑÐ°ÐµÑÑÑ Ð¿ÐµÑÐµÐ´ Ð¿ÐµÑÐ²Ð¾Ð¹
    backward() {
        for (let i = this.slides.length - 1; i > 0; i--) {
            if (this.slides[i].tagName !== 'BUTTON') {
                this.container.prepend(this.slides[i]);
                break;
            }
        }
        this.active();
    }

    // Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÑ Ð´Ð¾Ð¿Ð¾Ð»Ð½Ð¸ÑÐµÐ»ÑÐ½ÑÐ¹ css-ÐºÐ»Ð°ÑÑ Ð´Ð»Ñ Ð¿ÐµÑÐ²Ð¾Ð³Ð¾ ÑÐ»Ð°Ð¹Ð´Ð°
    active() {
        for (let i = 0; i < this.slides.length; i++) {
            this.slides[i].classList.remove(this.current);
        }
        this.slides[0].classList.add(this.current);
    }
}