import Slider from './Slider';

export default class MainSlider extends Slider {

    constructor({container, next}) {
        super({container: container, next: next});
        this.logo = document.querySelector('.sidecontrol > a');
        if (this.logo === null) this.error = true;
        this.showHanson();
    }

    // ÑÐºÑÑÐ²Ð°ÐµÑ Ð±Ð»Ð¾Ðº Ñ css-ÐºÐ»Ð°ÑÑÐ¾Ð¼ hanson, ÐºÐ¾ÑÐ¾ÑÑÐ¹ ÑÐ°ÑÐ¿Ð¾Ð»Ð¾Ð¶ÐµÐ½ Ð½Ð° ÑÑÐµÑÑÐµÐ¼ ÑÐ»Ð°Ð¹Ð´Ðµ
    // Ð¸ Ð¿Ð¾ÐºÐ°Ð·ÑÐ²Ð°ÐµÑ ÐµÐ³Ð¾ ÑÐµÑÐµÐ· ÑÑÐ¸ ÑÐµÐºÑÐ½Ð´Ñ Ð¿Ð¾ÑÐ»Ðµ Ð¿ÐµÑÐµÑÐ¾Ð´Ð° Ðº ÑÑÐµÑÑÐµÐ¼Ñ ÑÐ»Ð°Ð¹Ð´Ñ
    showHanson() {
        let hanson = document.querySelector('.hanson');
        if (!hanson) {
            return;
        }
        hanson.style.opacity = '0';
        hanson.classList.add('animated');
        // ÐºÐ°Ð¶Ð´ÑÐµ 100 Ð¼Ñ Ð·Ð°Ð¿ÑÑÐºÐ°ÐµÐ¼ Ð°Ð½Ð¾Ð½Ð¸Ð¼Ð½ÑÑ ÑÑÐ½ÐºÑÐ¸Ñ, ÐºÐ¾ÑÐ¾ÑÐ°Ñ Ð±ÑÐ´ÐµÑ Ð¿ÑÐ¾Ð²ÐµÑÑÑÑ
        // Ð·Ð½Ð°ÑÐµÐ½Ð¸Ðµ Ð¸Ð½Ð´ÐµÐºÑÐ° ÑÐµÐºÑÑÐµÐ³Ð¾ ÑÐ»Ð°Ð¹Ð´Ð°; ÐµÑÐ»Ð¸ ÑÑÐ¾ ÑÑÐµÑÐ¸Ð¹ ÑÐ»Ð°Ð¹Ð´ â ÑÐµÑÐµÐ· ÑÑÐ¸
        // ÑÐµÐºÑÐ½Ð´Ñ Ð±Ð»Ð¾Ðº .hanson Ð±ÑÐ´ÐµÑ Ð¿Ð¾ÐºÐ°Ð·Ð°Ð½, Ð¸Ð½Ð°ÑÐµ ÑÑÐ¾Ñ Ð±Ð»Ð¾Ðº Ð±ÑÐ´ÐµÑ ÑÐºÑÑÑ
        setInterval(() => {
            if (this.slideIndex === 2) {
                setTimeout(() => {
                    hanson.style.opacity = '1';
                    hanson.classList.add('slideInUp');
                }, 3000);
            } else {
                hanson.style.opacity = '0';
                hanson.classList.remove('slideInUp');
            }
        }, 100);
    }

    render() {
        super.render();
        if (this.error) return;

        // Ð¿ÐµÑÐµÑÐ¾Ð´ Ðº Ð¿ÐµÑÐ²Ð¾Ð¼Ñ ÑÐ»Ð°Ð¹Ð´Ñ Ð¿ÑÐ¸ ÐºÐ»Ð¸ÐºÐµ Ð½Ð° Ð»Ð¾Ð³Ð¾ÑÐ¸Ð¿
        this.logo.addEventListener('click', (event) => {
            event.preventDefault();
            this.slideIndex = 0;
            this.showSlide();
        });
    }
}