export default class Forms {
    constructor() {
        // ÑÐ¾ÑÐ¼Ñ Ð½Ð° ÑÑÑÐ°Ð½Ð¸ÑÐµ, Ñ ÐºÐ¾ÑÐ¾ÑÑÐ¼Ð¸ Ð±ÑÐ´ÐµÐ¼ ÑÐ°Ð±Ð¾ÑÐ°ÑÑ
        this.forms = document.querySelectorAll('form');
        // Ð²ÑÐµ Ð¿Ð¾Ð»Ñ Ð²ÑÐµÑ ÑÐ¾ÑÐ¼, Ñ ÐºÐ¾ÑÐ¾ÑÑÐ¼Ð¸ Ð±ÑÐ´ÐµÐ¼ ÑÐ°Ð±Ð¾ÑÐ°ÑÑ
        this.inputs = document.querySelectorAll('input'),
        // ÑÐ¾Ð¾Ð±ÑÐµÐ½Ð¸Ñ Ð¿ÑÐ¸ Ð¾ÑÐ¿ÑÐ°Ð²ÐºÐµ Ð´Ð°Ð½Ð½ÑÑ ÑÐ¾ÑÐ¼Ñ Ð½Ð° ÑÐµÑÐ²ÐµÑ
        this.message = {
            loading: 'ÐÑÐ¿ÑÐ°Ð²ÐºÐ° Ð´Ð°Ð½Ð½ÑÑ ÑÐ¾ÑÐ¼Ñ...',
            success: 'Ð¡Ð¿Ð°ÑÐ¸Ð±Ð¾! Ð¡ÐºÐ¾ÑÐ¾ Ð¼Ñ Ñ Ð²Ð°Ð¼Ð¸ ÑÐ²ÑÐ¶ÐµÐ¼ÑÑ',
            failure: 'Ð§ÑÐ¾-ÑÐ¾ Ð¿Ð¾ÑÐ»Ð¾ Ð½Ðµ ÑÐ°Ðº...'
        };
        // Ð¿ÑÑÑ Ðº Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸ÐºÑ Ð´Ð°Ð½Ð½ÑÑ Ð½Ð° ÑÐµÑÐ²ÐµÑÐµ
        this.handler = 'assets/question.php';
    }

    async postData(url, data) {
        let res = await fetch(url, {
            method: 'POST',
            body: data
        });
        return await res.text();
    }

    // Ð¾ÑÐ¸ÑÐ°ÐµÑ Ð²ÑÐµ Ð¿Ð¾Ð»Ñ input Ð²ÑÐµÑ ÑÐ¾ÑÐ¼ Ð½Ð° ÑÑÑÐ°Ð½Ð¸ÑÐµ
    clearInputs() {
        this.inputs.forEach(item => {
            item.value = '';
        });
    };

    initPhoneMask() {
        const phoneInputs = document.querySelectorAll('[name="phone"]');
        phoneInputs.forEach(input => {
            // Ð¿ÐµÑÐµÐ´ ÑÐµÐ¼, ÐºÐ°Ðº Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»Ñ Ð½Ð°ÑÐ½ÐµÑ Ð²Ð²Ð¾Ð´Ð¸ÑÑ, Ð¿ÐµÑÐµÐ¼ÐµÑÐ°ÐµÐ¼ ÐºÑÑÑÐ¾Ñ Ð² ÐºÐ¾Ð½ÐµÑ
            input.addEventListener('beforeinput', () => {
                input.setSelectionRange(input.value.length, input.value.length);
            });
            // ÑÑÐ½ÐºÑÐ¸Ñ mask Ð¾ÑÐ¸ÑÐ°ÐµÑ Ð¸ ÑÐ¾ÑÐ¼Ð°ÑÐ¸ÑÑÐµÑ Ð²Ð²Ð¾Ð´ Ð½Ð¾Ð¼ÐµÑÐ° ÑÐµÐ»ÐµÑÐ¾Ð½Ð° Ð² Ð¿Ð¾Ð»Ðµ input
            input.addEventListener('input', phoneMask);
            input.addEventListener('focus', phoneMask);
            input.addEventListener('blur',phoneMask);
        });

        // ÑÑÐ½ÐºÑÐ¸Ñ Ð¾ÑÐ¸ÑÐ°ÐµÑ Ð¸ ÑÐ¾ÑÐ¼Ð°ÑÐ¸ÑÑÐµÑ Ð²Ð²Ð¾Ð´ Ð½Ð¾Ð¼ÐµÑÐ° ÑÐµÐ»ÐµÑÐ¾Ð½Ð° Ð² Ð¿Ð¾Ð»Ðµ
        // input, Ð²ÑÐ·ÑÐ²Ð°ÐµÑÑÑ Ð¿ÑÐ¸ ÑÐ¾Ð±ÑÑÐ¸ÑÑ blur, focus, input
        function phoneMask(event) {
            let phonePattern = '+7 (___) ___-__-__',
                // Ð·Ð´ÐµÑÑ Ð±ÑÐ´ÐµÑ ÑÐ¾Ð»ÑÐºÐ¾ ÑÐ¸ÑÑÐ°(Ñ) ÑÐµÐ»ÐµÑÐ¾Ð½Ð½Ð¾Ð³Ð¾ ÐºÐ¾Ð´Ð° ÑÑÑÐ°Ð½Ñ
                countryCode = phonePattern.replace(/\D/g, ''),
                // Ð¸Ð· Ð²ÑÐµÐ³Ð¾ Ð²Ð²Ð¾Ð´Ð° Ð² Ð¿Ð¾Ð»Ðµ input Ð¾ÑÑÐ°Ð²Ð»ÑÐµÐ¼ ÑÐ¾Ð»ÑÐºÐ¾ ÑÐ¸ÑÑÑ
                onlyDigits = this.value.replace(/\D/g, ''),
                // Ð¿Ð¾Ð·Ð¸ÑÐ¸Ñ ÑÐºÐ°Ð·Ð°ÑÐµÐ»Ñ Ð½Ð° ÑÐµÐºÑÑÐ¸Ð¹ ÑÐ¸Ð¼Ð²Ð¾Ð» Ð² onlyDigits
                digitsIndex = 0;

            // Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»Ñ ÐµÑÐµ Ð½Ðµ Ð²Ð²ÐµÐ» Ð½Ð¸ Ð¾Ð´Ð½Ð¾Ð¹ ÑÐ¸ÑÑÑ, Ð² ÑÑÐ¾Ð¼ ÑÐ»ÑÑÐ°Ðµ Ð¼Ñ ÑÑÑÐ°Ð½Ð°Ð²Ð»Ð¸Ð²Ð°ÐµÐ¼ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ðµ
            // onlyDigits ÑÐ°Ð²Ð½ÑÐ¼ countryCode; ÑÑÐ¾ Ð¿Ð¾Ð·Ð²Ð¾Ð»Ð¸Ñ Ð¿Ð¾ÐºÐ°Ð·Ð°ÑÑ Ð² Ð¿Ð¾Ð»Ðµ Ð²Ð²Ð¾Ð´Ð° Ð¿Ð¾Ð´ÑÐºÐ°Ð·ÐºÑ +7 â
            // Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»Ñ Ð±ÑÐ´ÐµÑ Ð¿Ð¾Ð½ÑÑÐ½Ð¾, ÑÑÐ¾ ÐºÐ¾Ð´ Ð³Ð¾ÑÐ¾Ð´Ð° Ð²Ð²Ð¾Ð´Ð¸ÑÑ Ð½Ðµ Ð½ÑÐ¶Ð½Ð¾
            if (countryCode.length >= onlyDigits.length) {
                onlyDigits = countryCode;
            }

            /*
            * ÐÐµÑÐµÐ¼ ÑÐ°Ð±Ð»Ð¾Ð½ Ð½Ð¾Ð¼ÐµÑÐ° ÑÐµÐ»ÐµÑÐ¾Ð½Ð° +7 (___) ___-__-__ Ð¸ Ð¿ÑÐ¾ÑÐ¾Ð´Ð¸Ð¼ Ð¿Ð¾ Ð²ÑÐµÐ¼ ÑÐ¸Ð¼Ð²Ð¾Ð»Ð°Ð¼ ÑÑÐ¾Ð¹
            * ÑÑÑÐ¾ÐºÐ¸. ÐÑÐ»Ð¸ Ð¾ÑÐµÑÐµÐ´Ð½Ð¾Ð¹ ÑÐ¸Ð¼Ð²Ð¾Ð» ÑÐ¸ÑÑÐ° (ÐºÐ¾Ð´ Ð³Ð¾ÑÐ¾Ð´Ð°) Ð¸Ð»Ð¸ Ð¿Ð¾Ð´ÑÐµÑÐºÐ¸Ð²Ð°Ð½Ð¸Ðµ â Ð·Ð°Ð¼ÐµÐ½ÑÐµÐ¼ ÐµÐ³Ð¾
            * Ð½Ð° ÑÐ¸ÑÑÑ Ð¸Ð· onlyDigits. ÐÐ¾Ð·Ð¸ÑÐ¸Ñ Ð¾ÑÐµÑÐµÐ´Ð½Ð¾Ð³Ð¾ ÑÐ¸Ð¼Ð²Ð¾Ð»Ð° (ÑÐ¸ÑÑÑ) Ð¸Ð· onlyDigits ÑÑÐ°Ð½Ð¸Ð¼ Ð²
            * digitsIndex â Ð¸ ÑÐ¼ÐµÑÐ°ÐµÐ¼ ÑÐºÐ°Ð·Ð°ÑÐµÐ»Ñ Ð¿ÑÐ¸ ÐºÐ°Ð¶Ð´Ð¾Ð¹ Ð·Ð°Ð¼ÐµÐ½Ðµ Ð¿Ð¾Ð´ÑÐµÑÐºÐ¸Ð²Ð°Ð½Ð¸Ñ Ð½Ð° ÑÐ¸ÑÑÑ.
            */
            let phoneValue = phonePattern.replace(/./g, function(a) {
                // ÐµÑÐ»Ð¸ Ð¾ÑÐµÑÐµÐ´Ð½Ð¾Ð¹ ÑÐ¸Ð¼Ð²Ð¾Ð» ÑÐ²Ð»ÑÐµÑÑÑ ÑÐ¸ÑÑÐ¾Ð¹ Ð¸Ð»Ð¸ Ð¿Ð¾Ð´ÑÐµÑÐºÐ¸Ð²Ð°Ð½Ð¸ÐµÐ¼...
                let digitOrUnder = /[_0-9]/.test(a);
                // ...Ð¸ Ð¼Ñ ÐµÑÐµ Ð½Ðµ Ð´Ð¾ÑÐ»Ð¸ Ð´Ð¾ ÐºÐ¾Ð½ÑÐ° ÑÑÑÐ¾ÐºÐ¸ onlyDigits
                let notEndDigits = digitsIndex < onlyDigits.length;
                // ...ÑÐ¾ Ð¼Ñ Ð²Ð¾Ð·Ð²ÑÐ°ÑÐ°ÐµÐ¼ Ð¾ÑÐµÑÐµÐ´Ð½Ð¾Ð¹ ÑÐ¸Ð¼Ð²Ð¾Ð» Ð¸Ð· onlyDigits
                if (digitOrUnder && notEndDigits) {
                    // Ð¸ ÑÐ¼ÐµÑÐ°ÐµÐ¼ ÑÐºÐ°Ð·Ð°ÑÐµÐ»Ñ Ð´Ð»Ñ ÑÐ°Ð±Ð¾ÑÑ ÑÐ¾ ÑÐ»ÐµÐ´ÑÑÑÐ¸Ð¼
                    return onlyDigits.charAt(digitsIndex++);
                }
                // ÐµÑÐ»Ð¸ Ð² onlyDigits Ð²ÑÐµ ÑÐ¸Ð¼Ð²Ð¾Ð»Ñ (ÑÐ¾ ÐµÑÑÑ ÑÐ¸ÑÑÑ) Ð·Ð°ÐºÐ¾Ð½ÑÐ¸Ð»Ð¸ÑÑ, ÑÐ¾
                // Ð²ÑÐµ ÑÑÐ¾ Ð¾ÑÑÐ°Ð»Ð¾ÑÑ Ð² ÑÐ°Ð±Ð»Ð¾Ð½Ðµ phonePattern Ð·Ð°Ð¼ÐµÐ½ÑÐµÐ¼ Ð½Ð° Ð¿ÑÑÑÐ¾ÑÑ; Ð²
                // Ð¸ÑÐ¾Ð³Ðµ Ð¿Ð¾Ð»ÑÑÐ°ÐµÑÑÑ +7 (926) 765-43-__ ââ> +7 (926) 765-43
                if (digitsIndex >= onlyDigits.length) {
                    return '';
                }
                // ÐµÑÐ»Ð¸ Ð¾ÑÐµÑÐµÐ´Ð½Ð¾Ð¹ ÑÐ¸Ð¼Ð²Ð¾Ð» ÑÐ°Ð±Ð»Ð¾Ð½Ð° Ð½Ðµ Ð±ÑÐ» Ð¿Ð¾Ð´ÑÐµÑÐºÐ¸Ð²Ð°ÐµÐ½Ð¸ÐµÐ¼ Ð¸ ÑÐ¸Ð¼Ð²Ð¾Ð»Ñ
                // (ÑÐ¸ÑÑÑ) Ð² onlyDigits ÐµÑÐµ Ð½Ðµ Ð·Ð°ÐºÐ¾Ð½ÑÐ¸Ð»Ð¸ÑÑ â Ð·Ð½Ð°ÑÐ¸Ñ ÑÑÐ¾ Ð¿ÑÐ¾Ð±ÐµÐ» Ð¸Ð»Ð¸
                // Ð´ÐµÑÐ¸Ñ Ð¸Ð»Ð¸ ÐºÑÑÐ³Ð»ÑÐµ ÑÐºÐ¾Ð±ÐºÐ¸ â Ð¸Ñ Ð¼Ñ Ð²Ð¾Ð·Ð²ÑÐ°ÑÐ°ÐµÐ¼ Ð±ÐµÐ· Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ð¹
                return a;
            });

            // ÑÐµÐ¿ÐµÑÑ Ð·Ð°Ð¼ÐµÐ½ÑÐµÐ¼ Ð²Ð²ÐµÐ´ÐµÐ½Ð½Ð¾Ðµ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ðµ Ð½Ð° ÑÑÑÐ¾ÐºÑ ÑÐ°Ð±Ð»Ð¾Ð½Ð°, Ð² ÐºÐ¾ÑÐ¾ÑÐ¾Ð¹ Ð¼Ñ
            // Ð·Ð°Ð¼ÐµÐ½Ð¸Ð»Ð¸ Ð²ÑÐµ Ð¿Ð¾Ð´ÑÐµÑÐºÐ¸Ð²Ð°Ð½Ð¸Ñ Ð½Ð° ÑÐ¸ÑÑÑ (Ð¸Ð»Ð¸ Ð¿ÑÐ¾Ð±ÐµÐ»Ñ, ÐµÑÐ»Ð¸ ÑÐ¸ÑÑ Ð¼Ð°Ð»Ð¾)
            this.value = phoneValue;

            // ÐµÑÐ»Ð¸ Ð¿Ð¾Ð»Ðµ ÑÐµÑÑÐµÑ ÑÐ¾ÐºÑÑ Ð²Ð²Ð¾Ð´Ð° â ÑÐ±Ð¸ÑÐ°ÐµÐ¼ Ð¸Ð· Ð½ÐµÐ³Ð¾ +7, ÑÑÐ¾Ð±Ñ ÑÑÐ°Ð»Ð¾ Ð¿ÑÑÑÑÐ¼
            if (event.type === 'blur') {
                if (this.value.length === 2) {
                    this.value = '';
                }
            }
        }
    }

    checkAllEmais() {
        const emailInputs = document.querySelectorAll('[type="email"]');

        emailInputs.forEach(input => {
            input.addEventListener('keydown', (event) => {
                const keys = ['Delete', 'Backspace', 'ArrowLeft', 'ArrowRight'];
                // ÑÐ°Ð·ÑÐµÑÐµÐ½Ð¾ Ð¸ÑÐ¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÑ Delete, Backspace Ð¸ ÑÑÑÐµÐ»ÐºÐ¸
                if (keys.includes(event.key)) {
                    return;
                }
                // Ð·Ð°Ð¿ÑÐµÑÐµÐ½Ð¾ Ð²Ð²Ð¾Ð´Ð¸ÑÑ Ð²ÑÐµ, ÐºÑÐ¾Ð¼Ðµ Ð»Ð°ÑÐ¸Ð½Ð¸ÑÑ, ÑÐ¸ÑÑ, ÑÐ¾ÑÐºÐ¸, Ð¿Ð¾Ð´ÑÐµÑÐºÐ¸Ð²Ð°Ð½Ð¸Ñ, Ð´ÐµÑÐ¸ÑÐ° Ð¸ @
                if (event.key && event.key.search(/[-_@.0-9a-z]/i) === -1) {
                    event.preventDefault();
                }
            });
        });
    }

    init() {
        this.initPhoneMask();
        this.checkAllEmais();

        this.forms.forEach(form => {
            form.addEventListener('submit', (event) => {
                event.preventDefault();
                // ÑÐ¾Ð·Ð´Ð°ÐµÐ¼ div-Ð±Ð»Ð¾Ðº, Ð² ÐºÐ¾ÑÐ¾ÑÐ¾Ð¼ Ð¿Ð¾ÐºÐ°Ð·ÑÐ²Ð°ÐµÐ¼ ÑÐ¾Ð¾Ð±ÑÐµÐ½Ð¸Ðµ
                let statusMessage = document.createElement('div');
                statusMessage.style.cssText = `
                    margin: 15px 0;
                    color: #f99;
                `;
                statusMessage.textContent = this.message.loading;
                form.before(statusMessage);
                // Ð¿Ð¾Ð»ÑÑÐ°ÐµÐ¼ Ð´Ð°Ð½Ð½ÑÐµ ÑÐ¾ÑÐ¼Ñ Ð´Ð»Ñ Ð¾ÑÐ¿ÑÐ°Ð²ÐºÐ¸ Ð½Ð° ÑÐµÑÐ²ÐµÑ
                const formData = new FormData(form);
                // Ð¾ÑÐ¿ÑÐ°Ð²Ð»ÑÐµÐ¼ Ð´Ð°Ð½Ð½ÑÐµ ÑÐ¾ÑÐ¼Ñ Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸ÐºÑ Ð½Ð° ÑÐµÑÐ²ÐµÑÐµ
                this.postData(this.handler, formData)
                    .then(response => { // Ð´Ð°Ð½Ð½ÑÐµ ÑÐ¾ÑÐ¼Ñ Ð¾ÑÐ¿ÑÐ°Ð²Ð»ÐµÐ½Ñ ÑÑÐ¿ÐµÑÐ½Ð¾
                        console.log(response);
                        statusMessage.textContent = this.message.success;
                    })
                    .catch(() => statusMessage.textContent = this.message.failure) // Ð¾ÑÐ¸Ð±ÐºÐ°
                    .finally(() => { // ÑÐºÑÑÑÑ ÑÐ¾Ð¾Ð±ÑÐµÐ½Ð¸Ðµ, Ð¾ÑÐ¸ÑÑÐ¸ÑÑ Ð²ÑÐµ ÑÐ¾ÑÐ¼Ñ
                        this.clearInputs();
                        setTimeout(() => {
                            statusMessage.remove();
                        }, 5000);
                    });
            });
        });
    }
}